.profileContainer {
  padding: 1rem;
  margin-bottom: 1rem;
}

.kidsContainer{
  padding: 1rem;
  margin-top: 1rem;
  & ul {
    list-style: none;
  }
}

.kid{
  padding: .75rem;
  margin: 1rem 0;
  display: flex;
  flex-direction: column;
  & h6 {
    font-size: 13px;
    font-weight: 500;
  }
  & a {
    box-sizing: border-box;
    color: #000;
    padding: 0.75rem;
    margin: 0 auto;
  }
}

.kidContainer{
  display: flex;
  margin-bottom: 0.5rem;
  & h5 {
    margin-right: .25rem;
    font-weight: 600;
  }
}