.menubutton {
  background-color: #5288c1;
  box-shadow: -1px 1px 6px 0px #4878aa;
  width: 100px;
  height: 100px;
  margin: .5rem;
  border-radius: 15px;
  text-decoration: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  & span {
    color: var(--tg-theme-text-color);
    letter-spacing: 0;
    font-size: 14px;
  }
  & svg {
    position: absolute;
    box-sizing: border-box;
    width: 90px;
    height: 90px;
    color: #fff;
    opacity: 0.1;
  }
}

.borderboxshadow {
  box-shadow: 1px 1px 13px -5px;
  border-radius: 15px;
}

.lkbutton {
  background-color: goldenrod;
  box-shadow: -1px 1px 6px 0px rgb(193, 146, 29);
}

.lds-facebook {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-facebook div {
  display: inline-block;
  position: absolute;
  left: 8px;
  width: 16px;
  background: #fff;
  animation: lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
}
.lds-facebook div:nth-child(1) {
  left: 8px;
  animation-delay: -0.24s;
}
.lds-facebook div:nth-child(2) {
  left: 32px;
  animation-delay: -0.12s;
}
.lds-facebook div:nth-child(3) {
  left: 56px;
  animation-delay: 0;
}
@keyframes lds-facebook {
  0% {
    top: 8px;
    height: 64px;
  }
  50%, 100% {
    top: 24px;
    height: 32px;
  }
}